	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import aboutus from '@/views/aboutus/list'
	import fangyuanxinxi from '@/views/fangyuanxinxi/list'
	import fangwuzulin from '@/views/fangwuzulin/list'
	import zuke from '@/views/zuke/list'
    import menu_manage from '@/views/menu_manage/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import yuyuekanfang from '@/views/yuyuekanfang/list'
	import discussfangyuanxinxi from '@/views/discussfangyuanxinxi/list'
	import zulinhetong from '@/views/zulinhetong/list'
	import systemintro from '@/views/systemintro/list'
	import messages from '@/views/messages/list'
	import fangwuzixun from '@/views/fangwuzixun/list'
	import fangdongshensu from '@/views/fangdongshensu/list'
	import fangdong from '@/views/fangdong/list'
	import config from '@/views/config/list'
	import usersCenter from '@/views/users/center'
	import fangdongRegister from '@/views/fangdong/register'
	import fangdongCenter from '@/views/fangdong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/usersCenter',
			name: '',
			component: usersCenter
		}
		,{
			path: '/fangdongCenter',
			name: '',
			component: fangdongCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/aboutus',
			name: '',
			component: aboutus
		}
		,{
			path: '/fangyuanxinxi',
			name: '',
			component: fangyuanxinxi
		}
		,{
			path: '/fangwuzulin',
			name: '',
			component: fangwuzulin
		}
		,{
			path: '/zuke',
			name: '',
			component: zuke
		}
        ,{
            path: '/menu',
            name: '',
            component: menu_manage
        }
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/yuyuekanfang',
			name: '',
			component: yuyuekanfang
		}
		,{
			path: '/discussfangyuanxinxi',
			name: '-',
			component: discussfangyuanxinxi
		}
		,{
			path: '/zulinhetong',
			name: '',
			component: zulinhetong
		}
		,{
			path: '/systemintro',
			name: '',
			component: systemintro
		}
		,{
			path: '/messages',
			name: '',
			component: messages
		}
		,{
			path: '/fangwuzixun',
			name: '',
			component: fangwuzixun
		}
		,{
			path: '/fangdongshensu',
			name: '',
			component: fangdongshensu
		}
		,{
			path: '/fangdong',
			name: '',
			component: fangdong
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		]
	},
	{
		path: '/fangdongRegister',
		name: '',
		component: fangdongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
