<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="50%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="24">
						<el-form-item label="" prop="fangyuanbianhao">
							<el-input class="list_inp" v-model="form.fangyuanbianhao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.fangyuanbianhao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fangwumingcheng">
							<el-input class="list_inp" v-model="form.fangwumingcheng" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.fangwumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item prop="fangwutupian"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.fangwutupian?true:false"
								action="file/upload"
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="form.fangwutupian?form.fangwutupian:''" 
								@change="fangwutupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="/m3" prop="fangwumianji">
							<el-input class="list_inp" v-model="form.fangwumianji" placeholder="/m3"
                                type="text"
								:readonly="!isAdd||disabledForm.fangwumianji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fabushijian">
							<el-date-picker
								class="list_date"
								v-model="form.fabushijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.fabushijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fangwudizhi">
							<el-input class="list_inp" v-model="form.fangwudizhi" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.fangwudizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zulinfangshi">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.zulinfangshi?true:false"
								v-model="form.zulinfangshi" 
								placeholder=""
								>
								<el-option v-for="(item,index) in zulinfangshiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zulinjiage">
							<el-input class="list_inp" v-model.number="form.zulinjiage" placeholder=""
                                type="number"
                                @mousewheel.native.prevent
								:readonly="!isAdd||disabledForm.zulinjiage?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item prop="huxing"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.huxing?true:false"
								action="file/upload"
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="form.huxing?form.huxing:''" 
								@change="huxingUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="shifouyourenzulin">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.shifouyourenzulin?true:false"
								v-model="form.shifouyourenzulin" 
								placeholder=""
								>
								<el-option v-for="(item,index) in shifouyourenzulinLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fangdong">
							<el-input class="list_inp" v-model="form.fangdong" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.fangdong?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fangdongxingming">
							<el-input class="list_inp" v-model="form.fangdongxingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.fangdongxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="shoujihao">
							<el-input class="list_inp" v-model="form.shoujihao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.shoujihao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="youxiang">
							<el-input class="list_inp" v-model="form.youxiang" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.youxiang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zulinrenshu">
							<el-input class="list_inp" v-model.number="form.zulinrenshu" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zulinrenshu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fulladdress">
							<el-input class="list_inp" v-model="form.fulladdress" placeholder="" readonly @click.native="mapClick" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fangwujieshao">
							<editor :value="form.fangwujieshao" placeholder="" :readonly="!isAdd||disabledForm.fangwujieshao?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'fangwujieshao')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
		<locationForm ref="location" :position="form" @mapData="getAddress"></locationForm>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'fangyuanxinxi'
	const formName = ''
	//
	import locationForm from '@/components/common/location.vue'
	
	const location = ref(null)
	
	const mapClick = () => {
		if(!isAdd.value||disabledForm.fulladdress){
			return false
		}
		location.value.mapShow()
	}
	
	const getAddress = (e) => {
		form.value.longitude = e.lng
		form.value.latitude = e.lat
		form.value.fulladdress = e.fulladdress
	}
	//form
	const form = ref({})
	const disabledForm = ref({
        fangyuanbianhao : false,
        fangwumingcheng : false,
        fangwutupian : false,
        fangwumianji : false,
        fabushijian : false,
        fangwudizhi : false,
        zulinfangshi : false,
        zulinjiage : false,
        huxing : false,
        fangwujieshao : false,
        shifouyourenzulin : false,
        fangdong : false,
        fangdongxingming : false,
        shoujihao : false,
        youxiang : false,
        clicktime : false,
        storeupNumber : false,
        discussNumber : false,
        longitude : false,
        latitude : false,
        fulladdress : false,
        zulinrenshu : false,
        sfsh : false,
        shhf : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		fangyuanbianhao: [
		],
		fangwumingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		fangwutupian: [
		],
		fangwumianji: [
		],
		fabushijian: [
		],
		fangwudizhi: [
		],
		zulinfangshi: [
		],
		zulinjiage: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		huxing: [
		],
		fangwujieshao: [
		],
		shifouyourenzulin: [
		],
		fangdong: [
		],
		fangdongxingming: [
		],
		shoujihao: [
			{ validator: context.$toolUtil.validator.mobile, trigger: 'blur' },
		],
		youxiang: [
			{ validator: context.$toolUtil.validator.email, trigger: 'blur' },
		],
		clicktime: [
		],
		storeupNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		discussNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		longitude: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		latitude: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		fulladdress: [
		],
		zulinrenshu: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		sfsh: [
		],
		shhf: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const fangwutupianUploadSuccess=(e)=>{
		form.value.fangwutupian = e
	}
	//
	const zulinfangshiLists = ref([])
	//
	const huxingUploadSuccess=(e)=>{
		form.value.huxing = e
	}
	//
	const shifouyourenzulinLists = ref([])

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			fangyuanbianhao: '',
			fangwumingcheng: '',
			fangwutupian: '',
			fangwumianji: '',
			fabushijian: '',
			fangwudizhi: '',
			zulinfangshi: '',
			zulinjiage: '',
			huxing: '',
			fangwujieshao: '',
			shifouyourenzulin: '',
			fangdong: '',
			fangdongxingming: '',
			shoujihao: '',
			youxiang: '',
			clicktime: '',
			longitude: '',
			latitude: '',
			fulladdress: '',
			zulinrenshu: '',
			shhf: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.fangwujieshao = res.data.data.fangwujieshao?(res.data.data.fangwujieshao.replace(reg,'../../../zufangguanli/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.fabushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='fangyuanbianhao'){
					form.value.fangyuanbianhao = row[x];
					disabledForm.value.fangyuanbianhao = true;
					continue;
				}
				if(x=='fangwumingcheng'){
					form.value.fangwumingcheng = row[x];
					disabledForm.value.fangwumingcheng = true;
					continue;
				}
				if(x=='fangwutupian'){
					form.value.fangwutupian = row[x];
					disabledForm.value.fangwutupian = true;
					continue;
				}
				if(x=='fangwumianji'){
					form.value.fangwumianji = row[x];
					disabledForm.value.fangwumianji = true;
					continue;
				}
				if(x=='fabushijian'){
					form.value.fabushijian = row[x];
					disabledForm.value.fabushijian = true;
					continue;
				}
				if(x=='fangwudizhi'){
					form.value.fangwudizhi = row[x];
					disabledForm.value.fangwudizhi = true;
					continue;
				}
				if(x=='zulinfangshi'){
					form.value.zulinfangshi = row[x];
					disabledForm.value.zulinfangshi = true;
					continue;
				}
				if(x=='zulinjiage'){
					form.value.zulinjiage = row[x];
					disabledForm.value.zulinjiage = true;
					continue;
				}
				if(x=='huxing'){
					form.value.huxing = row[x];
					disabledForm.value.huxing = true;
					continue;
				}
				if(x=='fangwujieshao'){
					form.value.fangwujieshao = row[x];
					disabledForm.value.fangwujieshao = true;
					continue;
				}
				if(x=='shifouyourenzulin'){
					form.value.shifouyourenzulin = row[x];
					disabledForm.value.shifouyourenzulin = true;
					continue;
				}
				if(x=='fangdong'){
					form.value.fangdong = row[x];
					disabledForm.value.fangdong = true;
					continue;
				}
				if(x=='fangdongxingming'){
					form.value.fangdongxingming = row[x];
					disabledForm.value.fangdongxingming = true;
					continue;
				}
				if(x=='shoujihao'){
					form.value.shoujihao = row[x];
					disabledForm.value.shoujihao = true;
					continue;
				}
				if(x=='youxiang'){
					form.value.youxiang = row[x];
					disabledForm.value.youxiang = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='storeupNumber'){
					form.value.storeupNumber = row[x];
					disabledForm.value.storeupNumber = true;
					continue;
				}
				if(x=='discussNumber'){
					form.value.discussNumber = row[x];
					disabledForm.value.discussNumber = true;
					continue;
				}
				if(x=='longitude'){
					form.value.longitude = row[x];
					disabledForm.value.longitude = true;
					continue;
				}
				if(x=='latitude'){
					form.value.latitude = row[x];
					disabledForm.value.latitude = true;
					continue;
				}
				if(x=='fulladdress'){
					form.value.fulladdress = row[x];
					disabledForm.value.fulladdress = true;
					continue;
				}
				if(x=='zulinrenshu'){
					form.value.zulinrenshu = row[x];
					disabledForm.value.zulinrenshu = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('fangdong')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.fangdong = json.fangdong
				disabledForm.value.fangdong = true;
			}
			if(json.hasOwnProperty('fangdongxingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.fangdongxingming = json.fangdongxingming
				disabledForm.value.fangdongxingming = true;
			}
			if(json.hasOwnProperty('shoujihao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.shoujihao = json.shoujihao
				disabledForm.value.shoujihao = true;
			}
			if(json.hasOwnProperty('youxiang')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.youxiang = json.youxiang
				disabledForm.value.youxiang = true;
			}
		})
		zulinfangshiLists.value = ",".split(',')
		shifouyourenzulinLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.fangwutupian!=null) {
			form.value.fangwutupian = form.value.fangwutupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(form.value.huxing!=null) {
			form.value.huxing = form.value.huxing.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.list_editor {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
